nav {
    padding: 1.5rem;
    position: fixed;
    top: 0%;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #fff;
}
.nav__user {
  padding: 0.5rem 1rem;
  display: flex;
  align-items: center;
  user-select: none;
}
.dropdown-menu {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1;
    display: none;
    min-width: 120px;
    padding: 8px 0;
    background-color: #fff;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    border-radius: 4px;
}
.welcome-user {
    position: relative;
    cursor: pointer;
}
.welcome-user:hover .dropdown-menu {
    display: block;
}

.dropdown-menu li {
    display: block;
    width: 150px;
    padding:5px;
}

.dropdown-menu li a {
    color: #333;
    text-decoration: none;
    transition: color 0.3s ease;
}

.dropdown-menu li a:hover {
    color: #2190ff;
}
.nav__logo {
    font-size: 2rem;
    font-weight: 600;
    color: #0d213f;
    cursor: pointer;
}
.nav__logo span {
    color: #3685fb;
}

.nav__icon {
    display: none;
    font-size: 2rem;
    cursor: pointer;
}
.nav__links {
    list-style: none;
    display: flex;
    align-items: center;
    gap: 1rem;
    font-size: 1rem;
    font-weight: 500;
}
.link a {
    padding: 0 1rem;
    color: #0d213f;
    transition: 0.3s;
}
.link a:hover {
    color: #3685fb;
}

.menu-dropdown {
  position: absolute;
  top: 100%;
  right: 0;
  margin-top: 0.25rem;
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  list-style: none;
  padding: 0.5rem 0;
  width: 150px;
  z-index: 100;
}

.menu-dropdown li {
  padding: 0.5rem 1rem;
  font-size: 0.95rem;
  color: #333;
  transition: background 0.2s;
}

.menu-dropdown li:hover {
  background: #f5f5f5;
}
@media (max-width: 768px) {
    .nav__logo {
        margin-right: auto;
    }

    .nav__icon {
        display: block;
    }

    .nav__links {
        display: none;
        flex-direction: column;
        align-items: flex-start;
        gap: 0.5rem;
    }

    .nav__links.active {
        display: flex;
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        margin: auto;
        background-color: #fff;
        flex-direction: column;
        align-items: center;
        padding: 1rem 0;
        border-radius: 0.5rem;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
        z-index: 999;
        animation: slideDown 0.5s ease-in-out; 
    }
}
@keyframes slideDown {
    0% {
        transform: translateY(-10px);
        opacity: 0;
    }
    100% {
        transform: translateY(0);
        opacity: 1;
    }
}
